import React from "react";
import { connect } from "react-redux";
import * as Actions from "../../store/Feature1/FeatureAction";
import { bindActionCreators } from "redux";
import CommonCard from "../../components/Controls/Card/CommonCard";
import TableComponent from "../../components/Controls/Table/Table";
import logo from "../../assets/images/reactjs.jpg";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import ToComponent from "../../components/ToComponent/ToComponent";
import Calender from "../../components/Controls/Calender/Calender";
import * as QuestionActions from "../../store/Question/QuestionAction";
import SimpleSnackbar from "../../components/Snackbar2/SimpleSnackbar";
import Timeline from "../../components/Timeline/timeline";
import SimpleSnackbar1 from "../../components/Snackbar/Snackbar1";
import Successmodal from "../../components/Successmodal/Succesmodal";
import DatePicker from "../../components/DatePicker/Datepicker";

class Home extends React.Component {
  state = {
    timer: "",
    options: [
      { value: "1 Min", label: "1 Min" },
      { value: "5 Min", label: "5 Min" },
      { value: "10 Min", label: "10 Min" },
    ],
  };

  componentDidMount() {
    this.props.QuestionAction.addQuestionData();
  }

  handlePagination = (e) => {};

  handleSelect = (e) => {
    this.setState({ timer: e.value });
  };
  render() {
    let inboundDatacolumn = [
      {
        Header: "NO",
        accessor: "no",
        minWidth: 100,
      },
      {
        Header: "First Name",
        accessor: "pname",
        minWidth: 100,
      },
      {
        Header: "Last Name",
        accessor: "lname",
        minWidth: 100,
      },
      {
        Header: "Patient DOB",
        accessor: "patientdob",
        minWidth: 100,
      },
    ];
    let inboundData = [];
    inboundData.push({
      no: 1,
      pname: "ksjghdj",
      lname: "ksjfjgkd",
      patientdob: "hfdbj",
    });
    inboundData.push({
      no: 2,
      pname: "ksjghdj",
      lname: "ksjfjgkd",
      patientdob: "hfdbj",
    });

    return (
      <div className="container my-5">
        <CommonCard
          cardTitle="24"
          titleFontSize={"41px"}
          cardImage={logo}
          cardContent="No of JD"
          cardPadding="5px"
        />
        <div>
          <DatePicker
            label="Start Date"
            format="dd - MMM - yyyy"
            name="datepicker"
            value={this.state.startDate}
            onChange={(date) => this.setState({ startDate: date })}
          />
        </div>
        <TableComponent
          data={inboundData}
          columns={inboundDatacolumn}
          handlePageClick={(e) => {
            this.handlePagination(e);
          }}
          totalPage={3}
        />
        <Timeline />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    FeatureReducer: state.FeatureReducer,
    QuestionReducer: state.QuestionReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    FeatureAction: bindActionCreators(Actions, dispatch),
    QuestionAction: bindActionCreators(QuestionActions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
