import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CloseIcon from "@material-ui/icons/Close";
import { element, objectOf } from "prop-types";

const styles = () => ({
  card: {
    border: "1px solid #00000080",
    borderRadius: "15px",
    maxHeight: "auto",
    maxWidth: 871,
    marginBottm: "20px",
  },
  btn: {
    display: "inline-block",
    backgroundColor: "black",
    width: "140px",
    color: "white",
    textAlign: "right",
    outlineStyle: "none !important",
    marginRight: 7,
    padding: "8px 0px",
    marginBottom: 5,
    fontSize: 15,
    borderRadius: "30px",
    textTransform: "none",
    fontFamily: "Poppins",
    "&:hover": {
      backgroundColor: "#ddd",
      color: "black",
    },
  },
});

const selected = {
  display: "none",
};

const selectedBtn = (styles) => {
  const selectedNames = Object.keys(selected);
  selectedNames.map((key) => {
    styles[key] = selected[key];
  });
};

const names = [
  createData("Android Studio"),
  createData("Java"),
  createData("Python"),
  createData("Angular JS"),
  createData("Algorithms"),
  createData("Swift"),
];

function createData(name) {
  return { name };
}

class ToComponent extends React.Component {
  cart = new Map();

  onClick = (e, name, index) => {
    e.preventDefault();
    let dupCart = this.cart;
    selectedBtn(document.getElementById(index).style);
    dupCart.set(name);
  };

  render() {
    const { classes } = this.props;

    return (
      <div>
        <Card className={classes.card}>
          <CardContent>
            {names.map((name, index) => (
              <span className={classes.btn} key={index} id={index}>
                <CloseIcon
                  style={{
                    paddingRight: 5,
                    paddingLeft: 4,
                    height: 25,
                    width: 30,
                  }}
                  onClick={(e) => this.onClick(e, name, index)}
                />
                {name.name}
              </span>
            ))}
          </CardContent>
        </Card>
      </div>
    );
  }
}

export default withStyles(styles)(ToComponent);
