import React from "react";
import "react-datepicker/dist/react-datepicker.css";
import PersonOutlineIcon from "@material-ui/icons/PersonOutline";
import PhoneIcon from "@material-ui/icons/Phone";
import EmailIcon from "@material-ui/icons/Email";
import "./Timeline.css";
import { Card, CardContent, Grid } from "@material-ui/core";

export default class Timeline extends React.Component {
  render() {
    return (
      <Card>
        <CardContent>
          <Grid
            item
            xs={12}
            sm={12}
            className="col-12"
            style={{
              display: "flex",
              alignItems: "center",
              textAlign: "center",
              justifyContent: "space-evenly",
              cursor: "pointer",
            }}
          >
            <div
              style={{
                width: "70px",
                height: "70px",
                borderRadius: "45px",
                backgroundColor: "#04724D29",
                Color: "#04724D",
              }}
            >
              <PersonOutlineIcon style={{ fontSize: "50px", margin: "10px" }} />
            </div>
            <div style={{ fontWeight: "bold" }}>Abinaya S</div>
            <div>
              <PhoneIcon />
              9999999999
            </div>
            <div>
              <EmailIcon />
              Anandham@gmail.com
            </div>
          </Grid>
          <div className="container">
            <h6 className="timeline-header">JD:Android Developer</h6>

            <div className="main-timeline">
              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">12 - Mar - 2021</h5>
                  </div>
                </div>

                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Posted</h5>
                  </div>
                </div>
              </div>

              <div className="timeline"></div>

              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">14 - Mar - 2021</h5>
                  </div>
                </div>
                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Resume Sourced</h5>
                  </div>
                </div>
              </div>

              <div className="timeline"></div>

              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">17 - Mar - 2021</h5>
                  </div>
                </div>
                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Invitation Send</h5>
                  </div>
                </div>
              </div>

              <div className="timeline"></div>

              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">22 - Mar - 2021</h5>
                    <h5 className="sub-title">22 - Mar - 2021</h5>
                    <h5 className="sub-title">23 - Mar - 2021</h5>
                  </div>
                </div>
                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Interview Session</h5>
                    <h5 className="sub-title">Round 1</h5>
                    <h5 className="sub-title">Round 2</h5>
                  </div>
                </div>
              </div>

              <div className="timeline"></div>

              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">30 - Mar - 2021</h5>
                  </div>
                </div>
                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Final Offers</h5>
                  </div>
                </div>
              </div>
              <div className="timeline"></div>

              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">31 - Mar - 2021</h5>
                  </div>
                </div>
                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Offer Accepted</h5>
                  </div>
                </div>
              </div>
              <div className="timeline"></div>

              <div className="timeline">
                <div className="date-content">
                  <div className="date-outer">
                    <h5 className="title">2 - Apr - 2021</h5>
                  </div>
                </div>
                <div className="date-content1">
                  <div className="date-outer">
                    <h5 className="title">Company Joined</h5>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }
}
